body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-weight: 100;
  position: relative;
  overflow: scroll;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#ffd89b), to(#19547b));
  background-image: -webkit-linear-gradient(top left, #ffd89b, #19547b);
  background-image: -o-linear-gradient(top left, #ffd89b, #19547b);
  background-image: linear-gradient(to bottom right, #ffd89b, #19547b);
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
}

html {
  height: 100%; 
  overflow: hidden;
}




